import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { getShibu, toogleLoading, toogleRender } from '../actions/rootActions';
import Item from './Item';

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.onScroll = this.onScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
    const { renderedOnce } = this.props;
    if (!renderedOnce) {
      this.props.getShibu();
      this.props.toogleRender();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  }

  onScroll() {
    const { loadingShibu } = this.props;
    if (
      window.innerHeight + document.documentElement.scrollTop
        === document.documentElement.offsetHeight
      && !loadingShibu
    ) {
      this.props.toogleLoading();
      this.props.getShibu();
    }
  }

  render() {
    const { shibuData, loadingShibu } = this.props;
    return (
      <div>
        <div className="container-fluid">
          <Item shibuData={shibuData} />
        </div>
        {loadingShibu ? (
          <div className="loading-container">
            <div className="loading" />
          </div>
        ) : null}
      </div>
    );
  }
}

Main.propTypes = {
  getShibu: PropTypes.func.isRequired,
  toogleLoading: PropTypes.func.isRequired,
  toogleRender: PropTypes.func.isRequired,
  loadingShibu: PropTypes.bool.isRequired,
  renderedOnce: PropTypes.bool.isRequired,
  shibuData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      src: PropTypes.string,
      favorites: PropTypes.bool,
    }),
  ).isRequired,
};

const mapStateToProps = state => ({
  shibuData: state.shibuData,
  loadingShibu: state.loadingShibu,
  renderedOnce: state.renderedOnce,
});
const mapDispachToProps = dispatch => ({
  getShibu: () => dispatch(getShibu()),
  toogleLoading: () => dispatch(toogleLoading()),
  toogleRender: () => dispatch(toogleRender()),
});

export default connect(
  mapStateToProps,
  mapDispachToProps,
)(Main);
