import axios from 'axios';
import uuid from 'uuid';

export const toogleRender = () => ({
  type: 'RENDERED_ONCE',
});

export const getShibu = () => (dispatch) => {
  axios.get('http://shibe.online/api/shibes?count=16').then(response => dispatch({
    type: 'GET_SHIBU',
    data: response.data.map(src => ({
      id: uuid(),
      src,
      favorites: false,
    })),
  }));
};

export const toogleFavorites = id => ({
  type: 'TOOGLE_FAVORITES',
  id,
});

export const toogleLoading = () => ({
  type: 'TOOGLE_LOADING',
});
