import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { toogleFavorites } from '../actions/rootActions';

const Item = (props) => {
  const { shibuData } = props;
  const startToogleFavorites = (id) => {
    props.toogleFavorites(id);
  };
  return (
    <div className="row">
      {shibuData.map(shibu => (
        <div key={shibu.id} className="img-container d-flex">
          <img
            onClick={() => startToogleFavorites(shibu.id)}
            src={shibu.src}
            className={`img-responsive rounded ${shibu.favorites ? 'favorite' : ''}`}
            alt="Shibu"
          />
          <i className="material-icons favorites-icon">favorite</i>
        </div>
      ))}
    </div>
  );
};

Item.propTypes = {
  toogleFavorites: PropTypes.func.isRequired,
  shibuData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      src: PropTypes.string,
      favorites: PropTypes.bool,
    }),
  ).isRequired,
};

const mapDispatchToProps = dispatch => ({
  toogleFavorites: id => dispatch(toogleFavorites(id)),
});

export default connect(
  null,
  mapDispatchToProps,
)(Item);
