import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Item from './Item';

const Favorites = (props) => {
  const { shibuData } = props;
  return (
    <div className="container-fluid">
      {shibuData.length > 0 ? <Item shibuData={shibuData} /> : 'You don\'t like any Shibu :('}
    </div>
  );
};

Favorites.propTypes = {
  shibuData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      src: PropTypes.string,
      favorites: PropTypes.bool,
    }),
  ).isRequired,
};

const mapStateToProps = state => ({ shibuData: state.shibuData.filter(shibu => shibu.favorites) });
export default connect(mapStateToProps)(Favorites);
