const defaultData = { shibuData: [], loadingShibu: true, renderedOnce: false };
const rootReducer = (state = defaultData, action) => {
  switch (action.type) {
    case 'RENDERED_ONCE':
      return Object.assign({}, state, {
        renderedOnce: true,
      });
    case 'GET_SHIBU':
      return Object.assign({}, state, {
        shibuData: [...state.shibuData, ...action.data],
        loadingShibu: false,
      });

    case 'TOOGLE_FAVORITES':
      return Object.assign({}, state, {
        shibuData: state.shibuData.map((shibu) => {
          if (shibu.id === action.id) {
            return {
              id: shibu.id,
              src: shibu.src,
              favorites: !shibu.favorites,
            };
          }
          return shibu;
        }),
      });
    case 'TOOGLE_LOADING':
      return Object.assign({}, state, {
        loadingShibu: true,
      });
    default:
      return state;
  }
};

export default rootReducer;
